.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #111;
  padding: 1rem 2rem;
  border-bottom: 1px solid #333;
  position: sticky;
  top: 0;
  z-index: 100;
}

.logo {
  font-size: 3rem;
  font-weight: bold;
  color: #ff90e8;
  display: flex;
  align-items: center;
}

.nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;
  margin: 0;
}

.nav-links a {
  text-decoration: none;
  color: white;
  font-size: 1.2rem;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #ff8a65;
  text-decoration: underline;
}
@media (max-width: 768px) {
  .navbar {
    justify-content: space-between; /* Keep layout intact */
    padding: 1rem 1.5rem; /* Reduce side padding slightly */
  }

  .nav-links {
    transform: translateX(-12px); /* Shift links left */
    gap: 1rem; /* Slightly tighter spacing */
    font-size: 0.9rem; /* Optional: smaller text for space */
  }

  .logo {
    font-size: 1.8rem; /* Optional: scale logo for mobile */
  }
}

